<template lang="pug">
    .teachers__content
        h4.teachers__title Преподаватели
        .teachers__filter
            .teachers__filter-item 
                label.label__filter Выберите департамент:
                select(@change='onChange()' v-model="selectedDepartmentId").teachers__filter-select
                    option(value=0) Все
                    option(:value='item.id' v-for='item in departments') {{item.name}}
            .teachers__filter-item
                label.label__filter Выберите преподавателя:
                select(v-model="selectedTeacher").teachers__filter-select
                    option(value=0) Все
                    option(:value="teacher.id" v-for='teacher in filteredTeachers') {{teacher.lastName}} {{teacher.firstName}}
        .teachers__container   
            ul.teachers__list
                li.teachers__item-names
                    .teachers__number #
                    .teachers__name ФИО преподавателя
                    .teachers__department  Департамент
                    .teachers__experience  Опыт работы
                swiper.swiper(:options="swiperOption")
                    swiper-slide(v-for='item in slidesNumber')
                        li.teachers__item(
                            v-for="teacher,idx in filteredTeachers"
                            v-if='(item-1)*steps <= idx && idx< item*steps'
                            :key="teacher.id"
                            )
                            teacher-item(
                                :index='idx+1'
                                :teacher='teacher'
                                :groupItemContainerVisible='teacherItemContainerVisible'
                                :departments='departments'
                            )
                    .swiper-pagination.swiper-pagination-bullets(slot="pagination")
        button(@click='addTeacher' id='addTeacher').add__teacher Добавить преподавателя
        add-teacher-form(
            v-if='addTeacherFormVisible'
            v-on:eventNewTeacher='eventNewTeacher'
            :departments='departments'
        )
        
</template>
<script>
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'
import { mapState, mapActions } from "vuex";
export default {
    components: {
        teacherItem: () => import("../../admin/teachers/teacher-item"),
        addTeacherForm: () => import("../../admin/teachers/add-teacher-form"),
        Swiper,
        SwiperSlide
    },
    created() {
        this.fetchTeachers(), 
        this.fetchDepartments()
    },
    data () {
        return {
            steps: 4,
            swiperOption: {
                pagination: {
                    el: '.swiper-pagination',
                    clickable: true,
                    renderBullet(index, className) {
                    return `<span class="${className} swiper-pagination-bullet-custom">${index + 1}</span>`
                    }
                }
            },
            teacherItemContainerVisible: false,
            addTeacherFormVisible: false,
            selectedTeacher: 0,
            selectedDepartmentId: 0
        }
    },
    computed: {
        ...mapState ("teachers", {
            teachers: state => state.teachers
        }
        ),
        ...mapState ("departments", {
            departments: state => state.departments
        }
        ),
        filteredTeachers: function() {
            return this.teachers
            //filter by department
            .filter(teacher => {
                return this.selectedDepartmentId == 0 || teacher.department.id == this.selectedDepartmentId;
            })
            //filter by teacher
            .filter(teacher => {
                return this.selectedTeacher == 0 || teacher.id == this.selectedTeacher;
            })
        },
        slidesNumber: function(){
                return (Math.floor(this.filteredTeachers.length/this.steps)==this.filteredTeachers.length/this.steps)?Math.floor(this.filteredTeachers.length/this.steps):Math.floor(this.filteredTeachers.length/this.steps)+1
        }
    },
    methods: {
        ...mapActions("teachers", ["fetchTeachers"]),
        ...mapActions("departments", ["fetchDepartments"]),
        addTeacher(){
            this.addTeacherFormVisible=!this.addTeacherFormVisible
            document.getElementById('addTeacher').setAttribute("disabled", "true")
        },
        eventNewTeacher() {
            this.addTeacherFormVisible=!this.addTeacherFormVisible
            document.getElementById('addTeacher').removeAttribute("disabled")
        },
        onChange() {
            this.selectedTeacher = 0
        }
    }
  }
</script>
